	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Happy New Year","date":"01 Jan 2001","slug":"/2001-01-01-happy-new-year/","tags":"Y2001"},"html":"<p>Happy New Year from the three of us here in Lüllau!</p>\n<p>Mama's 1 year (actually 14 months) of maternity leave is now officially over. Today is a holiday; she starts work tomorrow. Task 1: getting to the office without a car, so she can drive her company car home.</p>\n<p>Actually, Task 0 (tomorrow and 3 days a week from now on): get Christopher to day care on time. And for PapaScott: get to work by 7:30, so that he can pick up Christopher from day care on time.</p>"},"next":{"fields":{"title":"End of everything","date":"31 Dec 2000","slug":"/2000-12-31-end-of-everything/"}},"previous":{"fields":{"title":"The adventure begins","date":"02 Jan 2001","slug":"/2001-01-02-the-adventure-begins/"}}},{"node":{"fields":{"title":"End of everything","date":"31 Dec 2000","slug":"/2000-12-31-end-of-everything/","tags":"Y2000"},"html":"<p>Unlike last year, when we celebrated the millenium at Harburg General Hospital (when Mama was suffering from fever and was being cooled by cabbage leaves), we're celebrating this millenium at home. Both Mama and Christopher are recovering from colds, so we turned down an offer to go sledding with Christopher's uncle. </p>\n<p>We also won't be going to the fireworks at the harbor in Hamburg. That will be cold, crowded, and with kids throwing firecrackers around, more than a little dangerous. It seems that this time of year normally safety-conscious Germans take leave of their senses. First they put lit candles in their Christmas trees, then they throw firecrackers at one another on New Years Eve. Or fall down drunk into the River Elbe.</p>\n<p>Speaking of safety, Christopher is at this moment happily pulling up the little rubber anti-slip mats in the shower. Don't ask why...</p>\n<p>You don't believe the part about the cabbage leaves last year? Here's proof...</p>\n<p><a href=\"http://albums.photopoint.com/j/ViewPhoto?u=185392&a=1369040&p=14798052\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/12/cabbagepatch.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"cabbagepatch.jpg: \" /></a></p>"},"next":{"fields":{"title":"White stuff","date":"30 Dec 2000","slug":"/2000-12-30-white-stuff/"}},"previous":{"fields":{"title":"Happy New Year","date":"01 Jan 2001","slug":"/2001-01-01-happy-new-year/"}}},{"node":{"fields":{"title":"White stuff","date":"30 Dec 2000","slug":"/2000-12-30-white-stuff/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/12/snow122000.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"snow122000.jpg: \" /></p>\n<p>It's snowing again today, so we still have our fresh white landscape in the backyard. Northern Germany usually gets snow only every 7 years. This is now our second year in a row.</p>"},"next":{"fields":{"title":"Break on through...","date":"29 Dec 2000","slug":"/2000-12-29-break-on-through/"}},"previous":{"fields":{"title":"End of everything","date":"31 Dec 2000","slug":"/2000-12-31-end-of-everything/"}}}],"pathPrefix":"","first":false,"last":false,"index":1028,"pageCount":1101}}

